<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process219"></a>

<p><br></p>
<p><span class="rvts17">以眼还眼（</span><span class="rvts50">An Eye for an Eye</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:516,Y:416</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：年迈法师的灵魂（</span><span class="rvts20">Old Magician's Spirit</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">杀掉死亡之眼，为年迈法师报仇</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1282.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">与锯木厂大门高处的一个年迈法师的灵魂谈话，他会让你帮他除掉身旁那个</span><span class="rvts20"></span><span class="rvts21">死亡之眼（</span><span class="rvts20">Deadeye</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">射手。</span>
<br><span class="rvts21">成功为他复仇后，他会给根据你的不同选择需求，给予你不同的奖励：</span>
<br><span class="rvts20">1. </span><span class="rvts21">选择提升你的复仇心机。他会给你压力穿刺（</span><span class="rvts20">Pressure Spike</span><span class="rvts21">）技能卷轴。</span>
<br><span class="rvts20">2. </span><span class="rvts21">选择想要平息周围的疼痛。他会给你寒冷领域（</span><span class="rvts20">Global Cooling</span><span class="rvts21">）技能卷轴。</span>
<br><span class="rvts20">3. </span><span class="rvts21">选择你在寻找地震的力量。他会给你地震（</span><span class="rvts20">Earthquake</span><span class="rvts21">）技能卷轴。</span>
<br><span class="rvts20">4. </span><span class="rvts21">选择你想要收取源力作为回报。之后，你会吸取他身上的源力。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



